import React, { useContext, useEffect, useState, useRef } from "react";
import { MainContext } from "../Helpers/Context";
import { RiAddLine, RiDeleteBin4Fill, RiEdit2Fill } from "react-icons/ri";
import "./settings.css";
import Loader from "../../small_comps/loader/Loader";
import ModalCont from "../../small_comps/modal/ModalCont";
import { Notifications } from "react-push-notification";

const Settings = () => {
  let { loggedSchool, url, notifications } = useContext(MainContext);
  const [classes, setClasses] = useState([]);
  const [currentSession, setCurrentSession] = useState("");
  const [isLoading, setIsLoading] = useState(false);
  const [currentTerm, setCurrentTerm] = useState("");
  const [currentFee, setCurrentFee] = useState("");
  const [newFee, setNewFee] = useState("0");
  const [newClass, setNewClass] = useState("");
  const [fees, setFees] = useState([]);
  const [feeEdit, setFeeEdit] = useState([]);
  const [deleteClass, setDeleteClass] = useState("");
  const [sessionArray, setSessionArray] = useState([]);
  const [paymentMethodArr, setPaymentMethodArr] = useState([]);
  const [paymentMethod, setPaymentMethod] = useState("");
  const [paymentForArr, setPaymentForArr] = useState([]);
  const [paymentFor, setPaymentFor] = useState("");
  const [termArray, setTermArray] = useState([]);
  const [newFeeName, setNewFeeName] = useState("");
  const [newFeeAdd, setNewFeeAdd] = useState("");
  const [updateClass, setUpdateClass] = useState("");
  const [isLoading2, setIsLoading2] = useState(false);
  const dataFetchedRef = useRef(false);

  const fetchData = () => {
    setIsLoading(true);
    fetch(`${url}/main/settings?school=${loggedSchool?.replace(/ /g, "-")}`, {
      credentials: "include",
    })
      .then((res) => res?.json())
      .then((data) => {
        //  console.log(data);
        let classes1 = data?.message[0]?.classes;
        setCurrentSession(data?.message[0]?.current_session);
        setCurrentTerm(data?.message[0]?.current_term);
        setFees(new Map(JSON?.parse(data?.message[0]?.fees)));
        setTermArray(data?.message[0]?.terms?.split(","));
        setSessionArray(data?.message[0]?.sessions?.split(","));
        setPaymentMethodArr(data?.message[0]?.payment_method?.split(","));
        setPaymentForArr(data?.message[0]?.payment_for?.split(","));
        setClasses(JSON?.parse(classes1));
        setIsLoading(false);
      })
      .catch((err) => {
        setIsLoading(false);
        console.log(err);
      });
  };

  useEffect(() => {
    if (dataFetchedRef.current) return;
    dataFetchedRef.current = true;
    fetchData();
  });
  let saveRequest = (data, updateName, id = "") => {
    setIsLoading2(true);
    fetch(`${url}/main/settings/save`, {
      credentials: "include",
      headers: {
        "Content-Type": "application/json",
      },
      method: "POST",
      body: JSON?.stringify({
        data: Array?.from(data),
        name: updateName?.trim()?.toLowerCase(),
      }),
    })
      .then((res) => res.json())
      .then((data) => {
        setIsLoading2(false);
        if (data?.status) {
          notifications?.success(data.message);
          document
            .getElementsByClassName(`${id}`)[0]
            .classList.remove("show-modal");
          document.body.style.overflow = "auto";
          document.body.style.top = "";
        } else {
          notifications?.warning(data.message);
        }
      })
      .catch((err) => {
        setIsLoading2(false);
        console.log(err);
      });
  };
  let saveFees = () => {
    if (
      newFee === "" ||
      newFee === "0" ||
      newFee === 0 ||
      parseInt(currentFee) === parseInt(newFee)
    ) {
    } else {
      let tempSet = fees.set(feeEdit, newFee);
      saveRequest(fees, "fees", "one");
      setFees(tempSet);
      setNewFee("");
    }
  };
  let saveNewFee = () => {
    if (newFeeAdd === "" || newFeeName === "") {
      return;
    } else {
      try {
        let tempSet = fees.set(newFeeName?.toLowerCase()?.trim(), newFeeAdd);
        saveRequest(fees, "fees", "add-fee");
        setFees(tempSet);
        setNewFeeName("");
        setNewFeeAdd("");
      } catch (error) {
        console.log(error);
      }
    }
  };
  let saveNewClass = () => {
    if (newClass === "") {
      return;
    } else {
      try {
        saveRequest(
          [...classes, newClass?.toLowerCase()?.trim()],
          "classes",
          "two"
        );
        setClasses([...classes, newClass?.toLowerCase()?.trim()]);
        setNewFee("");
        setNewClass("");
      } catch (error) {
        console.log(error);
      }
    }
  };
  let saveNewSession = () => {
    if (currentSession === "" || sessionArray.includes(currentSession)) {
      return;
    } else {
      saveRequest([...sessionArray, currentSession], "sessions", "three");
      setSessionArray([...sessionArray, currentSession]);
      console.log("get on up");
    }
  };
  let savePaymentMethod = () => {
    if (paymentMethod === "" || paymentMethodArr.includes(paymentMethod)) {
      return;
    } else {
      saveRequest(
        [...paymentMethodArr, paymentMethod?.toLowerCase()?.trim()],
        "payment_method",
        "four"
      );
      setPaymentMethodArr([
        ...paymentMethodArr,
        paymentMethod?.toLowerCase()?.trim(),
      ]);
    }
  };
  let handleEdit = (id, feeValue) => {
    setCurrentFee(feeValue);
    setFeeEdit(id);
    document.getElementsByClassName("one")[0].classList.add("show-modal");
    document.body.style.overflow = "hidden";
    window.scrollTo(0, parseInt(window.scrollY || "0") * -1);
  };
  let savePaymentFor = () => {
    if (paymentFor === "" || paymentForArr.includes(paymentFor)) {
      return;
    } else {
      saveRequest(
        [...paymentForArr, paymentFor?.toLowerCase()?.trim()],
        "payment_for",
        "five"
      );
      setPaymentForArr([...paymentForArr, paymentFor?.toLowerCase()?.trim()]);

      document.getElementsByClassName("five")[0].classList.remove("show-modal");
      document.body.style.overflow = "auto";
      document.body.style.top = "";
    }
  };
  let saveTerm = () => {
    if (currentTerm === "" || termArray.includes(currentTerm)) {
      return;
    } else {
      saveRequest(
        [...termArray, currentTerm?.toLowerCase()?.trim()],
        "terms",
        "six"
      );
      setTermArray([...termArray, currentTerm?.toLowerCase()?.trim()]);

      document.getElementsByClassName("six")[0].classList.remove("show-modal");
      document.body.style.overflow = "auto";
      document.body.style.top = "";
    }
  };
  let handleDelete = (arr, id) => {
    if (id !== "") {
      if (arr === "fees") {
        if (window.confirm(`Do you want to delete ${id} fee`)) {
          fees.delete(id);
          setNewFee(0);
          saveRequest(fees, arr);
        }
      } else if (arr === "classes") {
        if (window.confirm(`Do you want to delete ${id} from class`)) {
          saveRequest(
            classes?.filter((elem) => elem !== id),
            arr
          );
          setClasses(classes?.filter((elem) => elem !== id));
          setNewClass("");
          setNewFee(0);
        }
      } else if (arr === "session") {
        if (window.confirm(`Do you want to delete ${id} from session`)) {
          saveRequest(
            sessionArray?.filter((elem) => elem !== id),
            "sessions"
          );
          setSessionArray(sessionArray?.filter((elem) => elem !== id));
          setNewFee(0);
        }
      } else if (arr === "paymentMethod") {
        if (window.confirm(`Do you want to delete ${id} from payment method`)) {
          setPaymentMethodArr(paymentMethodArr.filter((elem) => elem !== id));
          setNewFee(0);
          setPaymentMethod("");
          saveRequest(
            paymentMethodArr.filter((elem) => elem !== id),
            "payment_method"
          );
        }
      } else if (arr === "paymentFor") {
        if (window.confirm(`Do you want to delete ${id} from paymentType`)) {
          setPaymentForArr(paymentForArr.filter((elem) => elem !== id));
          setNewFee(0);
          saveRequest(
            paymentForArr.filter((elem) => elem !== id),
            "payment_for"
          );
          setPaymentFor("");
        }
      } else if (arr === "term") {
        if (window.confirm(`Do you want to delete ${id} from term`)) {
          setTermArray(termArray.filter((elem) => elem !== id));
          setNewFee(0);
          saveRequest(
            termArray.filter((elem) => elem !== id),
            "terms"
          );
        }
      }
    }
  };
  let changeCurrentSession = (value) => {
    if (
      window.confirm("Do you want to change the current session to " + value)
    ) {
      setCurrentSession(value);
      if (currentSession !== "") {
        setIsLoading2(true);
        fetch(`${url}/main/settings/edit-current-session`, {
          credentials: "include",
          headers: {
            "Content-Type": "application/json",
          },
          method: "POST",
          body: JSON.stringify({
            value,
          }),
        })
          .then((res) => res.json())
          .then((data) => {
            setIsLoading2(false);

            if (data.status) {
              notifications.success(data.message);
              document
                .getElementsByClassName("five")[0]
                .classList.remove("show-modal");
              document.body.style.overflow = "auto";
              document.body.style.top = "";
            } else notifications.warning(data.message);
          })
          .catch((err) => {
            setIsLoading2(false);

            console.log(err);
          });
      } else {
        notifications.warning("Make a selection");
      }
    }
  };
  let changeCurrentTerm = (value) => {
    if (window.confirm("Do you want to change the current term to " + value)) {
      setCurrentTerm(value);
      if (currentTerm !== "") {
        setIsLoading2(true);

        fetch(`${url}/main/settings/edit-current-term`, {
          credentials: "include",
          headers: {
            "Content-Type": "application/json",
          },
          method: "POST",
          body: JSON.stringify({
            value,
          }),
        })
          .then((res) => res.json())
          .then((data) => {
            setIsLoading2(false);

            if (data.status) {
              notifications.success(data.message);
              document
                .getElementsByClassName("five")[0]
                .classList.remove("show-modal");
              document.body.style.overflow = "auto";
              document.body.style.top = "";
            } else notifications.warning(data.message);
          })
          .catch((err) => {
            setIsLoading2(false);
            console.log(err);
          });
      } else {
        notifications.warning("Make a selection");
      }
    }
  };
  let handleAdd = (id) => {
    document.getElementsByClassName(id)[0].classList.add("show-modal");
    document.body.style.overflow = "hidden";
    window.scrollTo(0, parseInt(window.scrollY || "0") * -1);
    // document.getElementsByClassName(id)[0].style.height = window.innerHeight + "px";
    // console.log(window.innerHeight)
  };
  let handleAddFee = (id) => {
    document.getElementsByClassName(id)[0].classList.add("show-modal");
    document.body.style.overflow = "hidden";
    window.scrollTo(0, parseInt(window.scrollY || "0") * -1);
    // document.getElementsByClassName(id)[0].style.height = window.innerHeight + "px";
    // console.log(window.innerHeight)
  };
  let editClass = (e) => {
    if (deleteClass !== "") {
      document
        .getElementsByClassName("update-class")[0]
        .classList.add("show-modal");
      document.body.style.overflow = "hidden";
      window.scrollTo(0, parseInt(window.scrollY || "0") * -1);
    }
  };
  let saveUpdateClass = () => {
    if (updateClass !== "" || deleteClass !== "") {
      let tempArr = [];
      classes?.forEach((class1, index) => {
        if (class1?.toLowerCase() === deleteClass?.toLowerCase()) {
          class1 = updateClass?.toLowerCase()?.trim();
        }
        tempArr?.push(class1);
      });

      setClasses(tempArr);
      setIsLoading2(true);

      fetch(`${url}/main/settings/update-class`, {
        credentials: "include",
        headers: {
          "Content-Type": "application/json",
        },
        method: "POST",
        body: JSON.stringify({
          prev: deleteClass?.trim()?.toLowerCase(),
          newCls: updateClass?.trim()?.toLowerCase(),
          classes: tempArr,
        }),
      })
        .then((res) => res.json())
        .then((data) => {
          setIsLoading2(false);

          if (data.status) {
            notifications.success(data.message);
            document
              .getElementsByClassName("update-class")[0]
              .classList.remove("show-modal");
            document.body.style.overflow = "auto";
            document.body.style.top = "";
          } else notifications.warning(data.message);
        })
        .catch((err) => {
          setIsLoading2(false);

          console.log(err);
        });
    }
  };
  return (
    <div className="__settings">
      <Notifications position="top-right" />
      <h2>Settings</h2>
      <div className="container">
        <div className="classes">
          <h4 className="h5">Classes</h4>
          {isLoading ? (
            <div>
              <Loader.Loader />
            </div>
          ) : (
            <>
              <div className="add-class-cont">
                <select
                  id=""
                  value={deleteClass}
                  onChange={(e) => setDeleteClass(e.target.value)}
                  className="class-select"
                >
                  {classes &&
                    classes.map((elem) => (
                      <option key={elem + Math.random()} value={elem}>
                        {elem.toUpperCase()}
                      </option>
                    ))}
                </select>
                <div className="btn-add-cont">
                  <button onClick={(e) => handleAdd("two")} title="Add class">
                    {" "}
                    <span>Add</span>
                    <RiAddLine size={20} />
                  </button>
                  <button
                    title="Delete class"
                    onClick={(e) => handleDelete("classes", deleteClass)}
                  >
                    <RiDeleteBin4Fill size={20} fill="#ee2d2d" />
                  </button>
                  <button
                    className="tb-edit"
                    title="Edit"
                    onClick={(e) => editClass(e)}
                  >
                    {<RiEdit2Fill size={20} fill="orange" />}
                  </button>
                </div>
              </div>
              <h4 className="h6">Set Current Session</h4>
              <div className="add-session-cont">
                <select
                  id=""
                  value={currentSession}
                  onChange={(e) => changeCurrentSession(e.target.value)}
                >
                  {classes &&
                    sessionArray.map((elem) => (
                      <option key={elem + Math.random()} value={elem}>
                        {elem.toUpperCase()}
                      </option>
                    ))}
                </select>
                <div className="btn-add-cont">
                  <button onClick={(e) => handleAdd("three")} title="Add class">
                    {" "}
                    <span>Add</span>
                    <RiAddLine size={20} />
                  </button>
                  <button
                    title="Delete Session"
                    onClick={(e) => handleDelete("session", currentSession)}
                  >
                    {" "}
                    <RiDeleteBin4Fill size={20} fill="#ee2d2d" />
                  </button>
                </div>
              </div>
              <h4 className="h6">Add Payment Method</h4>
              <div className="add-payment-method-cont">
                <select
                  id=""
                  value={paymentMethod}
                  onChange={(e) => setPaymentMethod(e.target.value)}
                >
                  {paymentMethodArr &&
                    paymentMethodArr.map((elem) => (
                      <option key={elem + Math.random()} value={elem}>
                        {elem.toUpperCase()}
                      </option>
                    ))}
                </select>
                <div className="btn-add-cont">
                  <button onClick={(e) => handleAdd("four")} title="Add class">
                    {" "}
                    <span>Add</span>
                    <RiAddLine size={20} />
                  </button>
                  <button
                    title="Delete payment method"
                    onClick={(e) =>
                      handleDelete("paymentMethod", paymentMethod)
                    }
                  >
                    {" "}
                    <RiDeleteBin4Fill size={20} fill="#ee2d2d" />
                  </button>
                </div>
              </div>
              <h4 className="h6">Add Payment Type</h4>
              <div className="add-payment-for-cont">
                <select
                  id=""
                  value={paymentFor}
                  onChange={(e) => setPaymentFor(e.target.value)}
                >
                  {paymentForArr &&
                    paymentForArr.map((elem) => (
                      <option key={elem + Math.random()} value={elem}>
                        {elem.toUpperCase()}
                      </option>
                    ))}
                </select>
                <div className="btn-add-cont">
                  <button onClick={(e) => handleAdd("five")} title="Add class">
                    {" "}
                    <span>Add</span>
                    <RiAddLine size={20} />
                  </button>
                  <button
                    title="Delete payment type"
                    onClick={(e) => handleDelete("paymentFor", paymentFor)}
                  >
                    {" "}
                    <RiDeleteBin4Fill size={20} fill="#ee2d2d" />
                  </button>
                </div>
              </div>
              <h4 className="h6">Add Term</h4>
              <div className="add-term-cont">
                <select
                  id=""
                  value={currentTerm}
                  onChange={(e) => changeCurrentTerm(e.target.value)}
                >
                  {termArray &&
                    termArray.map((elem) => (
                      <option key={elem + Math.random()} value={elem}>
                        {elem.toUpperCase()}
                      </option>
                    ))}
                </select>
                <div className="btn-add-cont">
                  <button title="Add Term" onClick={(e) => handleAdd("six")}>
                    {" "}
                    <span>Add</span>
                    <RiAddLine size={20} />
                  </button>
                  <button
                    title="Delete Term"
                    onClick={(e) => handleDelete("term", currentTerm)}
                  >
                    {" "}
                    <RiDeleteBin4Fill size={20} fill="#ee2d2d" />
                  </button>
                </div>
              </div>
            </>
          )}
        </div>
        <div className="fees-cont">
          <h3 className="h5">Fees</h3>
          <button
            onClick={(e) => handleAddFee("add-fee")}
            title="Add Fee"
            className="btn btn-outline-dark btn-sm btn-add"
          >
            {" "}
            <span>Add</span>
            <RiAddLine size={20} />
          </button>
          <table
            border={1}
            cellSpacing={50}
            className="table table-hover table-responsive"
          >
            <thead>
              <tr>
                <th>Class</th>
                <th>amount</th>
                <th>action</th>
              </tr>
            </thead>
            <tbody>
              {isLoading ? (
                <tr className="loading">
                  <td colSpan={2}>
                    <Loader.Loader />
                  </td>
                </tr>
              ) : (
                <>
                  {Array.from(fees).map((item, index) => {
                    return (
                      <tr key={index + item}>
                        <td>{item[0]}</td>
                        <td className="td-input"> {item[1]} </td>
                        <td>
                          <div className="action-cont">
                            <div
                              className="tb-edit"
                              title="Edit"
                              onClick={(e) => handleEdit(item[0], item[1])}
                            >
                              Edit
                              <button>
                                {<RiEdit2Fill size={20} fill="orange" />}
                              </button>
                            </div>
                            <div
                              className="tb-del"
                              title="Delete"
                              onClick={(e) => handleDelete("fees", item[0])}
                            >
                              <button>
                                {<RiDeleteBin4Fill size={20} fill="#ee2d2d" />}
                              </button>
                            </div>
                          </div>
                        </td>
                      </tr>
                    );
                  })}
                </>
              )}
            </tbody>
          </table>
        </div>
        <ModalCont
          title={`Edit Class '${deleteClass}'`}
          save={saveUpdateClass}
          classModal="update-class"
          loading={isLoading2}
          btn={isLoading2 ? <Loader.Loader /> : "Save"}
        >
          <div className="inputs-container">
            <div className="input-cont">
              <label htmlFor="update-class">Current Class</label>
              <input
                type="text"
                id="update-class"
                value={deleteClass?.toUpperCase()}
                disabled
                className="disabled"
              />
            </div>
            <div className="input-cont">
              <label htmlFor="new-u-c">New Class</label>
              <input
                type="text"
                id="new-u-c"
                value={updateClass}
                onChange={(e) => setUpdateClass(e?.target?.value)}
              />
            </div>
          </div>
        </ModalCont>
        <ModalCont
          title={"Edit " + feeEdit + " fee"}
          save={saveFees}
          classModal="one"
          loading={isLoading2}
          btn={isLoading2 ? <Loader.Loader /> : "Save"}
        >
          <div className="inputs-container">
            {/* <div className="info"></div> */}
            <div className="input-cont">
              <label htmlFor="current-fee">Current Fee</label>
              <input type="text" id="current-fee" value={currentFee} disabled />
            </div>
            <div className="input-cont">
              <label htmlFor="new-fee">New Fee</label>
              <input
                type="number"
                id="new-fee"
                value={newFee}
                onChange={(e) => setNewFee(e.target.value)}
                maxLength={6}
              />
            </div>
          </div>
        </ModalCont>
        <ModalCont
          title={"Add Session"}
          classModal="three"
          save={saveNewSession}
          loading={isLoading2}
          btn={isLoading2 ? <Loader.Loader /> : "Add"}
        >
          <div className="inputs-container">
            <div className="input-cont">
              <label htmlFor="add-session">Add Session</label>
              <input
                type="text"
                id="add-session"
                value={currentSession}
                onChange={(e) => setCurrentSession(e.target.value)}
              />
            </div>
          </div>
        </ModalCont>
        <ModalCont
          title={"Add payment method"}
          classModal="four"
          save={savePaymentMethod}
          loading={isLoading2}
          btn={isLoading2 ? <Loader.Loader /> : "Add"}
        >
          <div className="inputs-container">
            <div className="input-cont">
              <label htmlFor="add-payment-method">Add payment Method</label>
              <input
                type="text"
                id="add-payment-method"
                value={paymentMethod}
                onChange={(e) => setPaymentMethod(e.target.value)}
              />
            </div>
          </div>
        </ModalCont>
        <ModalCont
          title={"Add payment type"}
          classModal="five"
          save={savePaymentFor}
          loading={isLoading2}
          btn={isLoading2 ? <Loader.Loader /> : "Add"}
        >
          <div className="inputs-container">
            <div className="input-cont">
              <label htmlFor="add-payment-type">Add payment type</label>
              <input
                type="text"
                id="add-payment-type"
                value={paymentFor}
                onChange={(e) => setPaymentFor(e.target.value)}
              />
            </div>
          </div>
        </ModalCont>
        <ModalCont
          title={"Add term"}
          classModal="six"
          save={saveTerm}
          loading={isLoading2}
          btn={isLoading2 ? <Loader.Loader /> : "Add"}
        >
          <div className="inputs-container">
            <div className="input-cont">
              <label htmlFor="add-term">Add Term</label>
              <input
                type="text"
                id="add-term"
                value={currentTerm}
                onChange={(e) => setCurrentTerm(e.target.value)}
              />
            </div>
          </div>
        </ModalCont>
        <ModalCont
          title={"Add Fee"}
          classModal="add-fee"
          save={saveNewFee}
          loading={isLoading2}
          btn={isLoading2 ? <Loader.Loader /> : "Add"}
        >
          <div className="inputs-container">
            <div className="input-cont">
              <label htmlFor="add-fee">Class Name</label>
              <input
                type="text"
                id="add-fee"
                value={newFeeName}
                onChange={(e) => setNewFeeName(e.target.value)}
              />
            </div>
            <div className="input-cont">
              <label htmlFor="add-new-fee">Fee</label>
              <input
                type="number"
                id="add-new-fee"
                value={newFeeAdd}
                onChange={(e) => setNewFeeAdd(e.target.value)}
                maxLength={8}
              />
            </div>
          </div>
        </ModalCont>
        <ModalCont
          title={"Add Class"}
          classModal="two"
          save={saveNewClass}
          loading={isLoading2}
          btn={isLoading2 ? <Loader.Loader /> : "Add"}
        >
          <div className="inputs-container">
            <div className="input-cont">
              <label htmlFor="add-class">Class Name</label>
              <input
                type="text"
                id="add-class"
                value={newClass}
                onChange={(e) => setNewClass(e.target.value)}
              />
            </div>
          </div>
        </ModalCont>
      </div>
    </div>
  );
};

export default Settings;
